import { disableScroll } from '../functions/disable-scroll';
import { enableScroll } from '../functions/enable-scroll';

(function () {
  const burgersList = document?.querySelectorAll('[data-burger]');
  const menu = document?.querySelector('[data-menu]');
  const nav = menu?.querySelector('.nav')
  const menuItems = document?.querySelectorAll('[data-menu-item]');
  const overlay = document?.querySelector('[data-menu-overlay]');
  let timeoutId = 0

  burgersList?.forEach(burger => {

    burger.addEventListener('click', () => {
      clearTimeout(timeoutId)

      if (menu?.classList.contains('menu--active')) {

        addAnimationHidden(burger)
        burger?.setAttribute('aria-expanded', 'false');
        burger?.setAttribute('aria-label', 'Открыть меню');
        enableScroll();
        hiddenMenu()

      } else {

        addAnimationVisible(burger)
        burger?.setAttribute('aria-expanded', 'true');
        burger?.setAttribute('aria-label', 'Закрыть меню');
        disableScroll();

      }
    });

    overlay?.addEventListener('click', (e) => {
      if (nav.contains(e.target)) return
      addAnimationHidden(burger)
      burger?.setAttribute('aria-expanded', 'false');
      burger?.setAttribute('aria-label', 'Открыть меню');
      burger.classList.remove('burger--active');
      enableScroll();
      hiddenMenu()
    });

    menuItems?.forEach(el => {
      el.addEventListener('click', () => {
        addAnimationHidden(burger)
        burger?.setAttribute('aria-expanded', 'false');
        burger?.setAttribute('aria-label', 'Открыть меню');
        burger.classList.remove('burger--active');
        hiddenMenu()
        enableScroll();
      });
    });

  })




  function addAnimationHidden(elem) {
    elem?.classList.remove('burger--active');
    overlay.classList.remove('fadeIn')
    overlay.classList.add('fadeOut')
    nav.classList.remove('fadeInLeft')
    nav.classList.add('fadeOutLeft')
  }
  function addAnimationVisible(elem) {
    elem?.classList.add('burger--active');
    menu?.classList.add('menu--active');
    overlay.classList.remove('fadeOut')
    overlay.classList.add('fadeIn')
    nav.classList.remove('fadeOutLeft')
    nav.classList.add('fadeInLeft')
  }
  function hiddenMenu() {
    timeoutId = setTimeout(() => {
      menu?.classList.remove('menu--active')
    }, 500)
  }
})();
