
const input = document.getElementById('suggest');
const cleaner = document.querySelector('.search__cleaner');
const results = document.querySelector('.search__results');
const search = document.querySelector('.search');
let timeoutId = 0

input.addEventListener('input', (e) => {
  clearTimeout(timeoutId)
  const value = e.target.value.trim()
  const valueLength = value.length

  results.classList.toggle('active', valueLength)
  cleaner.classList.toggle('active', valueLength)
  results.innerHTML = '<span style="padding:20px 10px">Поиск...</span>'

  timeoutId = setTimeout(() => {
    ymaps.suggest(value).then(function (items) {
      console.log(items)
      showResults(items)
    })
  }, 1000)
})
input.addEventListener('focus', (e) => {
  const value = e.target.value.trim()

  if (value) {
    results.classList.add('active')
  }
})
input.addEventListener('blur', (e) => {
  const value = e.target.value.trim()

  input.classList.toggle('value-full', value.length)
})

cleaner.addEventListener('click', () => {
  input.value = ''
  input.classList.remove('value-full')
  results.classList.remove('active')
  cleaner.classList.remove('active')
})

results.addEventListener('click', (e) => {
  const target = e.target
  const clickInItem = target.closest('.search__results-item')
  if (clickInItem) results.classList.remove('active')
})

document.addEventListener('click', (e) => {
  const clickInSearch = search.contains(e.target)
  if (!clickInSearch) results.classList.remove('active')
})


export function showResults(resultsData) {
  results.innerHTML = ''

  if (resultsData.length) {
    const list = document.createElement('ul')
    list.classList.add('list-reset')

    resultsData.forEach(obj => {
      const item = document.createElement('li')
      item.classList.add('search__results-item')
      item.tabIndex = '0'
      item.dataset.value = obj.value
      item.textContent = obj.displayName

      list.append(item)
    });
    results.append(list)
  } else {
    results.innerHTML = '<span style="padding:20px 10px">Нет результата по данному запросу</span>'
  }
}

