let headerFixedHidden = true
const header = document.querySelector('.header')
const nav = document.querySelector('.header .nav')
const headerFixed = document.querySelector('.header-fixed')
const siteContainer = document.querySelector('.site-container')
const heightHeader = header.offsetHeight

function activeHeaderFixed() {
  const burgerMenuActive = document.querySelector('.menu--active')
  if (burgerMenuActive) return

  const afterHeightHeader = window.scrollY > heightHeader * 2 + 30

  if (afterHeightHeader && headerFixedHidden) {

    headerFixedHidden = false
    siteContainer.style.paddingTop = heightHeader + 'px'
    header.classList.add('is-hidden')

    headerFixed.classList.add('active')
    headerFixed.classList.remove('fadeOutUp')
    headerFixed.classList.add('fadeInUp')

  } else if (!afterHeightHeader && !headerFixedHidden) {

    headerFixedHidden = true
    siteContainer.style.paddingTop = ''
    header.classList.remove('is-hidden')

    headerFixed.classList.remove('fadeInUp')
    headerFixed.classList.add('fadeOutUp')
    setTimeout(() => {
      headerFixed.classList.remove('active')
    }, 300)
  }
}

window.addEventListener('scroll', activeHeaderFixed)
