import { dataPoints } from '../data/data.js'

const defaultSettings = {
  zoom: 10,
  type: 'yandex#map',
  center: [55.748203, 37.615272]
}
const defaultPointSize = {
  iconImageSize: [30, 30],
  iconImageOffset: [-5, -5],
  iconImageHref: '../img/point-map.svg',
}

const zoomToPoint = 15
let curIdPoint = dataPoints[0].id
let prevIdPoint = ''

ymaps.ready(init)

function init() {
  const map = new ymaps.Map('map', { ...defaultSettings });
  const objectManager = new ymaps.ObjectManager({ clusterize: true });

  // настройка поиска

  renderAllPoints()
  // map.behaviors.disable('scrollZoom');
  renderBoxAddressList(dataPoints)

  objectManager.objects.options.set({
    iconLayout: 'default#image',
    iconImageHref: '../img/point-map.svg',
    ...defaultPointSize,
    hideIconOnBalloonOpen: false,
    // balloonOffset: [100, 100],
    balloonMinWidth: '293',
    balloonMaxWidth: '293',
    balloonMinHeight: '290',
    balloonMaxHeight: '290',
    balloonContentLayout: createBalloon(),
  });

  objectManager.objects.events
    .add('balloonopen', function (event) {
      resetStylesPrevPoint()
      onPointClick(event)
    })
  map.events.add('boundschange', function () {
    const points = getVisiblePoints()
    const data = getDataVisiblePoints(points)
    renderBoxAddressList(data)
  });

  objectManager.clusters.options.set({
    clusterIconLayout: createCluster(),
    clusterIconShape: createIconShape(),
  });

  function addSwitchedPointOnBox() {
    const addressList = document.querySelector('.address__list')
    const boxAddressItems = document.querySelectorAll('.box-address')

    boxAddressItems.forEach(boxAddress => {
      const pointActive = boxAddress.dataset.idPoint === addressList.dataset.idClick
      if (pointActive) {
        boxAddress.classList.add('active')
      }

      boxAddress.addEventListener('click', (e) => handlerBoxAddress(e))
      boxAddress.addEventListener('keypress', (e) => {
        const key = e.which || e.keyCode;
        if (key === 13) {
          e.currentTarget.click();
        }
        handlerBoxAddress(e)
      })
    })
    function handlerBoxAddress(event) {
      addressList.dataset.idClick = event.currentTarget.dataset.idPoint

      boxAddressItems.forEach(it => it.classList.remove('active'))
      event.currentTarget.classList.add('active')
      curIdPoint = event.currentTarget.dataset.idPoint

      resetStylesPrevPoint()
      setOptionsPoint(curIdPoint)
      // renderAllPoints()
      goToPoint(curIdPoint)
      prevIdPoint = curIdPoint
    }
  }
  function addToPointOnResults() {
    const inputSearch = document.querySelector('.search__input')
    const results = document.querySelector('.search__results')

    results.addEventListener('click', (e) => {
      const target = e.target
      const resultsItem = target.closest('.search__results-item')
      if (!resultsItem) return

      results.classList.remove('active')
      const pointName = resultsItem.dataset.value
      inputSearch.value = pointName

      ymaps.geocode(pointName, {
        boundedBy: map.getBounds(),
        strictBounds: true,
        results: 10
      }).then(function (res) {
        const coordinates = res
          .geoObjects
          .get(0)
          .geometry
          .getCoordinates()
        map.setCenter(coordinates, 14, { checkZoomRange: true })
      });

      resetStylesPrevPoint()
    })
  }
  function goToPoint(idPoint) {
    console.log(idPoint)
    const coordinates = objectManager.objects.getById(idPoint).geometry.coordinates
    map.setCenter(
      coordinates,
      zoomToPoint,
      { checkZoomRange: true });
  }

  function getVisiblePoints() {
    return ymaps.geoQuery(objectManager.objects).searchInside(map).map(function (object) {
      // console.log(object)
      return object
    })._objects
  }
  function getDataVisiblePoints(points) {
    const arrIdPoints = points.map(obj => obj.properties.get('id'))
    const visiblePointsData =
      dataPoints.filter(point => arrIdPoints.includes(point.properties.id))
    return visiblePointsData
  }
  function renderAllPoints() {
    dataPoints.forEach((obj) => {
      objectManager.add({ ...obj });
      map.geoObjects.add(objectManager);
    })
  }
  function createBoxAddressList(dataPoints) {
    const addressList = document.querySelector('.address__list')
    addressList.innerHTML = ''

    dataPoints.forEach(point => {
      const addressItem = document.createElement('li')
      addressItem.classList.add('address__item')

      const box = document.createElement('div')
      box.classList.add('box-address')
      box.tabIndex = '0'
      box.dataset.idPoint = point.id

      const title = document.createElement('h3')
      title.classList.add('box-address__title')
      title.textContent = point.properties.header

      const address = document.createElement('a')
      address.classList.add('box-address__address')
      address.textContent = point.properties.header
      address.href = point.properties.addressOnMap

      const boxList = document.createElement('ul')
      boxList.classList.add('list-reset', 'box-address__list')

      const boxItemClock = document.createElement('li')
      boxItemClock.classList.add('box-address__item', 'box-address__item_clock')

      point.properties.balloonTimeWork.forEach(time => {
        const span = document.createElement('span')
        span.classList.add('box-address__text')
        span.textContent = time
        boxItemClock.append(span)
      })

      const boxItemPhone = document.createElement('li')
      boxItemPhone.classList.add('box-address__item', 'box-address__item_phone')

      point.properties.balloonContacts.forEach(contact => {

        const phone = document.createElement('a')
        phone.classList.add('box-address__text')
        phone.href = 'tel:79992532312'
        phone.textContent = point.properties.balloonContacts
        boxItemPhone.append(phone)
      })

      addressList.append(addressItem)
      addressItem.append(box)
      box.append(title)
      box.append(address)
      box.append(boxList)
      boxList.append(boxItemClock)
      boxList.append(boxItemPhone)
    });
  }
  function renderBoxAddressList(dataPoints) {
    createBoxAddressList(dataPoints)
    addSwitchedPointOnBox()
    addToPointOnResults()
  }

  function resetStylesPrevPoint() {
    if (!prevIdPoint.length) return
    objectManager.objects.setObjectOptions(prevIdPoint, { ...defaultPointSize });
  }
  function setOptionsPoint(pointId) {
    objectManager.objects.setObjectOptions(pointId, {
      iconImageSize: [50, 50],
      iconImageOffset: [-15, -15],
      iconImageHref: '../img/point-map-choice.svg',
    });
  }
  function onPointClick(e) {
    const pointId = e.get('objectId')
    prevIdPoint = pointId
    setOptionsPoint(pointId)
    const target = e.get('target')
    const zoom = target._oldMapZoom

    if (zoom < zoomToPoint) {
      goToPoint(pointId)
    }
  }


  // Зададаем макет метки кластера
  function createCluster() {
    return ymaps.templateLayoutFactory.createClass(
      `<div class="cluster-map">
          <svg>
            <use xlink:href="#point-map" style="fill: #160b75">
            </use>
          </svg>
          <span class="cluster-map__number">
            {{ properties.geoObjects.length }}
          </span>
        </div>`,
    );
  }
  // Зададаем активную область иконки кластера
  function createIconShape() {
    return {
      type: 'Circle',
      coordinates: [12, 10],
      radius: 25,
    };
  }
  function createBalloon() {
    return ymaps.templateLayoutFactory.createClass(`
  <div class="box-address">
    <h3 class="box-address__title">
      {{ properties.header|default:"Название оптики" }}
    </h3>
    <a class="box-address__address" href="{{properties.addressOnMap}}">
      {{ properties.address | default:"Адрес оптики" }}
    </a>

    <ul class="list-reset box-address__list">
      <li class="box-address__item box-address__item_clock">
        <div class="box-address__item-title">
          Время работы
        </div>
        <div>
        {% for time in properties.balloonTimeWork %}
          <span class="box-address__text"> {{ time | default: '-' }}</span>
        {% endfor %}
        </div>
      </li>
      <li class="box-address__item box-address__item_phone">
        <div class="box-address__item-title">
          Контакты
        </div>
        {% for contact in properties.balloonContacts %}
          <a href="tel:{{ contact.replace(/\D/g, '') | default: "" }}" class="box-address__text">
            {{ contact }}
          </a>
        {% endfor %}
      </li>
    </ul>
  </div>
  `)
  }
  map.controls.remove('geolocationControl'); // удаляем геолокацию
  map.controls.remove('searchControl'); // удаляем поиск
  map.controls.remove('trafficControl'); // удаляем контроль трафика
  map.controls.remove('typeSelector'); // удаляем тип
  map.controls.remove('fullscreenControl'); // удаляем кнопку перехода в полноэкранный режим
  // map.controls.remove('zoomControl'); // удаляем контрол зуммирования
  map.controls.remove('rulerControl'); // удаляем контрол правил
  map.behaviors.disable(['scrollZoom']); // отключаем скролл карты (опционально)

}
